import type { ActionFunction, LoaderFunction } from "@remix-run/node";
import { Form, Link, useLoaderData } from "@remix-run/react";
import authenticator from "~/services/auth.server";

export let loader: LoaderFunction = async ({ request }) => {
  return await authenticator.isAuthenticated(request, {
    failureRedirect: "/login",
  });
};

export const action: ActionFunction = async ({ request }) => {
  await authenticator.logout(request, { redirectTo: "/posts" });
};

export default function AdminIndex() {
  const data = useLoaderData();
  return (
    <>
      <p>
        <div className="flex w-full justify-between">
          {" "}
          <p className="italic">user - {data?.name}</p>
          <Form method="post">
            <button className="py-2 px-4 rounded text-white font-bold bg-red-600">Log out</button>
          </Form>
        </div>{" "}
        <br />
        <button className="rounded bg-green-600 p-2">
          <Link to={"new"} className="text-white">
            Create a new Post
          </Link>
        </button>
      </p>
    </>
  );
}
